import info from "../../components/details/info.vue"
import parser from "@/components/jyf-parser/jyf-parser";
import good from "../../components/index/good.vue"
import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
export default {
	name: "",
	components: {
		info,
		"jyf-parser": parser,
		good,
		uniGoodsNav,
		uniPopup,
		uniNumberBox,
	},
	props: {},
	data() {
		return {
			goodDetail: [],
			explainList: [
				"30天无忧退货",
				"48小时快速退款",
				"满88包邮",
			],
			number: 1,
			options: [{
				icon: '../../static/images/collect.png',
				text: '收藏'
			}, {
				icon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/uni-ui/goodsnav/dianpu.png',
				text: '店铺'
			}, {
				icon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/uni-ui/goodsnav/carts.png',
				text: '购物车',
				info: null
			}],
			buttonGroup: [{
					text: '加入购物车',
					backgroundColor: '#ff0000',
					color: '#fff'
				},
				{
					text: '立即购买',
					backgroundColor: '#ffa200',
					color: '#fff'
				}
			]
		}
	},
	methods: {},
	methods: {
		// 获取商品数据
		goodDetails(id, openId) {
			this.$api.goodDetails(id, openId).then(res => {
				if (res.status === 200) {
					this.goodDetail = res.data
					if (res.data.collected) {
						this.options[0].icon = "../../static/images/collect-action.png"
					}
					this.history()

				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 登录提醒
		login() {
			uni.showModal({
				title: '暂未登录',
				content: '是否立即去登录？',
				success: function(res) {
					if (res.confirm) {
						uni.switchTab({
							url: "/pages/my/my"
						})
					} else if (res.cancel) {}
				}
			})
		},
		// 点击商品导航图标
		onClick(e) {
			if (!this.$store.state.openId) {
				this.login()
			} else {
				let a = {
					goodsId: this.goodDetail.info.id,
					openId: this.$store.state.openId
				}
				if (e.index === 0) {
					this.$api.addcollect(a).then(res => {
						if (res.status === 200) {
							if (this.goodDetail.collected) {
								this.options[0].icon = "../../static/images/collect.png"
								uni.showToast({
									title: "取消收藏成功"
								})
							} else {
								this.options[0].icon = "../../static/images/collect-action.png"
								uni.showToast({
									title: "收藏成功"
								})
							}
							this.goodDetail.collected = !this.goodDetail.collected
						}
					}).catch(err => {
						console.log(err)
					})
				} else if (e.index === 2) {
					uni.switchTab({
						url: "/pages/shoppingCar/shoppingCar"
					})
				}
			}

		},
		// 点击商品导航按钮
		buttonClick(e) {
			if (e.index === 1) {
				this.$refs.popup.open()
			} else {
				if (!this.$store.state.openId) {
					this.login()
				} else {
					this.addCart(1)
				}
			}
		},
		// 加入购物车
		addCart(number) {
			let a = {
				number: 1,
				goodsId: this.goodDetail.info.id,
				openId: this.$store.state.openId
			}
			a.number = Number(number)
			this.$api.addCart(a).then(res => {
				if (res.status === 200 && res.data.data === "success") {
					this.$store.state.shoppingNumber += Number(number)
					this.options[2].info = this.$store.state.shoppingNumber
					uni.showToast({
						title: "加入成功"
					})
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 改变商品数量
		bindChange(e) {
			this.number = Number(e.val)
		},
		// 关闭pop弹框
		close() {
			this.$refs.popup.close()
		},
		// 加入购物车或立即购买
		buy(num) {
			this.close()
			if (!this.$store.state.openId) {
				this.login()
			} else {
				if (num === 0) {
					this.addCart(this.number)
				} else {
					let a = {
						user_id: this.$store.state.openId,
						goods_id: this.goodDetail.info.id,
						goods_name: this.goodDetail.info.name,
						retail_price: this.goodDetail.info.retail_price,
						list_pic_url: this.goodDetail.info.list_pic_url,
						number: this.number
					}
					this.$store.state.orderList = [a]
					uni.navigateTo({
						url: "/pages/order/order"
					})

				}
			}
		},
		// 存历史浏览
		history() {
			if (this.$store.state.openId) {
				let goodItem = {
					id: this.goodDetail.info.id,
					name: this.goodDetail.info.name,
					list_pic_url: this.goodDetail.info.list_pic_url,
					retail_price: this.goodDetail.info.retail_price,
					goods_brief: this.goodDetail.info.goods_brief,
					add_time: new Date(),
				}
				let historyShops = []
				let storage = uni.getStorageSync("historyShops")
				console.log(storage)
				if (storage) {
					historyShops = storage
					let userIndex = historyShops.findIndex(item=>item.userId === this.$store.state.openId)
					if (userIndex !== -1) {
						let index = historyShops[userIndex].goods.findIndex(item => item.id === this.goodDetail.info.id)
						if (index === -1) {
							historyShops[userIndex].goods.unshift(goodItem);
						}
					} else {
						historyShops.unshift({
							userId: this.$store.state.openId,
							goods: [goodItem]
						})
					}
				} else {
					historyShops.unshift({
						userId: this.$store.state.openId,
						goods: [goodItem]
					})
				}
				uni.setStorageSync("historyShops",historyShops)
			}
		}
	},
	onLoad(option) {
		this.goodDetails(option.id, this.$store.state.openId)
		if (this.$store.state.shoppingNumber) {
			this.options[2].info = this.$store.state.shoppingNumber
		}
	}

	,

	filters: {}

	,
	computed: {}

	,
	watch: {}

	,
	directives: {}
}
